html {
    height: 100%;
}

body {
    font-family: $global_ff1;
    font-size: 14px;
    color: $global_color;
    letter-spacing: .1em;
    height: 100%;
    min-width: 320px;
}
.rtl body { font-family: $global_ff1_rtl;}

h1, h2, h3, h4, h5, h6 {
    text-align: center;
    color: $global_color;
    line-height: 1.4;
}
h1 { font-size: 36px; }
h2 { font-size: 30px; }
h3 { font-size: 24px; }
h4 { font-size: 18px; }
h5 { font-size: 14px; }
h6 { font-size: 12px; }

main {
    padding-top: $global_header_height + 20;
    margin-bottom: 30px;
    &.home {
        padding-top: 0;
    }
}

a {
    text-decoration: none;
    color: inherit;
    transition: all 300ms;
    &:hover {
        text-decoration: underline;
        transition: all 300ms;
        color: #55cb9d;
    }
    &.disabled {
        pointer-events: none;
        cursor: default;
        color: #999;
    }
}

.page-wrapper {
    min-height: 100%;
    margin-bottom: -$global_footer_height;
}
.page-buffer {
    height: $global_footer_height;
}

.ff-1 { font-family: $global_ff1; }
.ff-2 { font-family: $global_ff2; }

.rtl .ff-1 { font-family: $global_ff1_rtl; }
.rtl .ff-2 { font-family: $global_ff2_rtl; }



.container {
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}

.g-btn {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    white-space: nowrap;
    padding: 8px 5px 0;
    font-size: 34px;
    line-height: 1.4;
    background: #56cc9d;
    color: #fff;
    text-transform: uppercase;
    // border-radius: 5px;
    // border: 1px solid transparent;
    &:hover {
        opacity: .6;
        color: #fff;
        text-decoration: none;
    }
    &:focus {
        //outline: thin dotted;
    }
    &--lang {
        font-size: 23px;
        color: #fff;
        background: #000;
        padding: 4px 7px 0;
        border: 0;
    }
}

.dropdown-menu {
    display: none;
    &.open {
        display: block;
    }
}


.breadcrumb {
    display: none;
}










.logo {
    margin-bottom: 40px;
}


.home-block {
    text-align: center;
    padding-top: 50px;
    margin-bottom: 50px;
    h2 {
        font-size: 50px;
        text-transform: uppercase;
    }
    h3 {
        font-size: 35px;
        text-transform: uppercase;
    }
    p {
        font-size: 30px;
    }
    .slogan {
        font-size: 33px;
        text-transform: uppercase;
        p {
            padding: 15px 0;
            font-size: 45px;
        }
    }
    .row-img {
        margin-bottom: 30px;
        img {
            margin: 20px 0;
        }
        p {
            font-size: 40px;
        }
    }
    .btn-wrap {
        margin: 30px auto;
    }
    .product-menu ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: baseline;
        li {
            margin: 0 5px;
            .img {
                height: 138px;
                width: 138px;
                display: table-cell;
                vertical-align: bottom;
                img {
                    max-height: 100%;
                    max-width: 100%;
                }
            }
            .name {
                font-size: 30px;
                max-width: 150px;
            }
        }
    }
}
.home-block-1 {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    padding: 180px 0 100px;
    color: #fff;
    height: 624px;
    position: relative;
    .container {
        position: relative;
        z-index: 10;
    }
    .social {
        text-align: right;
        position: absolute;
        bottom: -50px;
        right: 0;
        a:hover { opacity: .7; }
    }
    &:after {
        content: "";
        display: block;
        height: 96%;
        width: 93%;
        background: #38353a;
        opacity: .5;
        position: absolute;
        z-index: 1;
        bottom: 0;
        right: 0;
    }
}

.home-block-2 {
    .ff-2 {
        font-size: 38px;
    }
}

.home-block-3 {
    .floor {
        p { font-size: 30px; }
        .nis { font-size: 40px; }
    }
}

.home-block-4 {
    position: relative;
    .title {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
    .row-img p {
        font-size: 33px;
        max-width: 240px;
        margin: 0 auto;
    }
    .join_us {
        max-width: 500px;
        margin: 0 auto;
        line-height: 2em;
        a {
            position: relative;
            top: 20px
        }
    }
}

.home-block-5 {
    h2 {
        font-size: 31px;
        max-width: 550px;
        margin: 0 auto 30px;
    }
}